import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, ObjectKeys, qryStore, hasWritePrivilege } from "../../../stores/Utils";

import { SvgIcon_Cmpt, CheckBox_Cmpt } from "../../../components";

import { OpenModal } from "../../../components/DialogHRM";

import "./styles.css"
import {  getall_toPay, search_operations, SumDocuments } from "../services/server_fetching";
import {  PayManual_Cmpt, PayMultiManual_Cmpt } from "..";
import { getall_subaccount, get_balances_by_type } from "../../CuentasContables/services/server_fetching";
import ToPayItem from "./ToPayItem";
import { PickProvider_Cmpt } from "../../ComprobanteParametrizados";
import WithScroll from "../../../components/scroll-decorator";







const PayManual = PayManual_Cmpt();

const DropInput = PickProvider_Cmpt();

const Svg = SvgIcon_Cmpt();

const Acc2Pay = "Acc2Pay"
const CheckBox = CheckBox_Cmpt();


const CollectList  = ({ data, elmId, updObs }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const [loading, set_loading] = useState(false);
	const [multiProv, set_multiProv] = useState(false);


	

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			updStore("list_show_type_pay","pending")
			
			get_balances_by_type("global_accounts","global_account_params",cb_result);
			set_obs(gen16CodeId());
			getall_subaccount();
		}
	});


	const openUpdateView =  () => {
		
	}


	


	

	const handleChange =  (v) => {
		!loading && set_loading(true)
	}


	const handle_search =  (v) => {
		set_loading(true)
		search_operations(cb_result)
	}


	const handle_getall=  (v) => {
		getall_toPay(getStore(Acc2Pay), cb_result)
	}

	const cb_result = async (f, v) => {
		set_obs(gen16CodeId())
		set_loading(false);
	};


	




	return (

		<div className="">
			<div className="accounts_header _dsplFlx" >
				<div>
					<h2>Pagos</h2>
				</div>
				<div className="flexSpace"/>
				
			</div>


			
			<div className="" style={{maxWidth: "720px", padding: 16}}>
				<div className={""} style={{height:190}}>
					<MenuAcc handle_getall={handle_getall} data={data} cb_result={cb_result} updObs={set_obs}/>
				</div>
				
				
				<div className="accounts_container ">
					{getStore(Acc2Pay)?.account && getStore("toPay_by_doc") && ObjectKeys(getStore("toPay_by_doc"))?.map(cid=>{
						let docAmount = getStore("toPay_by_doc")[cid].amount*1;
						if((getStore("list_show_type_pay")==="done" && docAmount === 0) || (getStore("list_show_type_pay")==="pending" && docAmount !== 0)){
							return (
								<ToPayItem 
									data={getStore("toPay_by_doc")[cid]} 
									elmId={cid} 
									key={cid}
									openUpdateView={openUpdateView}
								/>
							)
						}
						else{
							return null;
						}
					})}
				</div>
			</div>
			
		</div>
	);
};

export default CollectList;




const MenuAcc = ({handle_getall, data, cb_result, updObs}) =>{

	const [obs, set_obs] = useState(0);
	const [float, setFloat] = useState(false);

	let accountD = getStore("account_ids")?.[getStore(Acc2Pay)?.account];
	let subaccountD = getStore("sub_account_ids")?.[getStore(Acc2Pay)?.subAccount];

	

	const openAddView =  () => {
		
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "93vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = <PayManual confirm={handle_getall} item={data} />
		OpenModal(dataModal);
	}
	
	const editSearchAccount = () => {
		updStore(Acc2Pay, null);
		set_obs(gen16CodeId())
	}


	const changeViewType = (e) => {
		updStore("list_show_type_pay",e)
		updObs(gen16CodeId())
	}

	const handleScroll = (e) => {
		if(e>140){
			setFloat(true);
		}else{
			setFloat(false)
		}
	}


	const handleSearchAccount = (v) => {
		let nn= {
			"account":v?.account,
			"subAccount":v?.subAccount,
			providerId:v?.providerId
		};
		updStore(Acc2Pay, nn);
		getall_toPay(nn,cb_result);
	}

	let idKey = getStore(Acc2Pay)?.account + "_"+ getStore(Acc2Pay)?.subAccount;

	return (
		<div className={`menu_pay ${float?"_floating":""}`} >
		<div className="_dsplFlx " style={{margin: "18px 0"}}>
				<div className=" acc_dtls">
					<div className="_dsplFlx"  >
						<div className="code" style={{color: "#646cff", cursor:"pointer"}}  
							
						>
							{accountD?.code} 
						</div>
						<div className="name">
							{accountD?. name}
						</div>
					</div>
					<div className="flexSpace"/>
					{subaccountD ?
					<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
						<div className="code" >
							{subaccountD?.subAccount} 
						</div>
						<div className="name">
							{subaccountD?. subAccountName}
						</div>
					</div>
					:null}
					{getStore(Acc2Pay)?.account && SumDocuments(getStore("toPay_by_doc"),"amount",getStore(Acc2Pay)?.providerId)?
					<div className="_dsplFlx ">
						<div className="_dsplFlx " style={{padding: "35px 18px 10px "}}>
							<div className="flexSpace"/>
							<CheckBox 
								data={[
									{label: "Pendientes", id:"pending"},
									{label: "Pagados", id:"done"}
								]}
								active={getStore("list_show_type_pay") || "pending"}
								updSelect={(e)=>changeViewType(e)}
							/>
							<div className="flexSpace"/>
						</div>
						
					</div>
					
					:null}



				</div>
				<div className="flexSpace"/>
				<div>
					<div className="_dsplFlx acc_dtls">
						<div className="name">
							Por Pagar:
						</div>
						<div className="code"   style={{margin: "0px 0 0 7px", color: "#646cff"}}>
							{getStore("rec_by_acc")?.[idKey] && !isNaN(getStore("rec_by_acc")?.[idKey]*-1)?  `$${(getStore("rec_by_acc")?.[idKey]*-1).toFixed(2)}`: null}
						</div>
					</div>
					
					
				</div>
			</div>

			<WithScroll scrollhandler={handleScroll} />
		</div>
	)
}